body{
	overflow-x: hidden;
}
.site-header{
	margin: 20px;
	border: 0;
	z-index: 2;
	.wrapper{
		min-width: 80%;
	}
}
.page-content{
	padding-top: 0;
	.wrapper{
		padding: 0;
		min-width: 100%;
	}
}

.home-copy, .portfolio-apps, .portfolio-labs{
	background-color: $background-color-light;
}

.page-link{
	border: none !important;
}

.center-image{
	/*margin: 0 auto;*/
	/*display: block;*/
}

.custom_middle_home{
	margin: 0 0;
	padding: 0 0;
	h4{
		/*font-size: 1em;*/
		color: #37c8f2;
		font-weight: 600;
	}
	p{
		font-size: 0.6em;
	}
	img{
		/*transform: scale(1.15);*/
		top: 0;
		bottom: 0;
	}
	.middle_copy{
		background-color: rgba(0, 200, 255, 0.2);
	}
	@media (min-width: 576px) {
		max-width: 500px;
		margin: 0 auto;
		padding: 0 auto;
	}
	@media (min-width: 768px) { 
		margin: 0 auto;
		padding: 0 auto;
		.middle_copy{
			background-color: #00000000;
		}

	}
}

.custom_contact_home{
	/*img{
		transform: scale(1.5);
	}*/
}

.container.custom_hero {
	/*max-width: 400px;*/
	background-color: #c8eef9;
	margin: 0 0;
	padding: 0 0;
	h1{
		font-weight: 400;
		color: #fa8269;
		font-size: 1.8em;
	}

	img{
		/*transform: scale(1.4);*/
	}

	.btn-primary{
		border-radius: 0;
		border-width: 0;
		font-weight: 600;
		background-color: #3233ff;
		color: #ffffff;
	}

	.hero_img_column{
		display: none;
	}
	.hero_copy_column{
		padding: 100px 50px;
		background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url("/assets/heroImg.png");
		background-blend-mode: normal;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}
	.span-hero{
		color: #ff6f37;
	}
	@media (min-width: 576px) {
		max-width: 500px;
		margin: 0 auto;
		padding: 0 auto;
	}
	@media (min-width: 768px) { 
		margin: 0 auto;
		padding: 0 auto;
		img{
			transform: scale(1.4);
		}
		max-width: 720px;
		.hero_img_column{
			display: block;
		}
		.hero_copy_column{
			background-image: none;
		}
		h1{
			font-weight: 200;
			color: #000000;
		}
		.span-hero{
			color: #37c8f2;
		}

	}
	@media (min-width: 992px) { 
		max-width: 800px;
	}
	@media (min-width: 1200px) { 
		max-width: 940px;
	}
}

